<template class="dark">
  <div class="common-layout">
    <el-container>
      <el-aside width="200px">
        <h5 class="mb-2">导航</h5>
        <el-menu default-active="2" class="el-menu-vertical-demo">
          <el-menu-item index="1" @click='go_home()'>
            <el-icon>
              <HomeFilled />
            </el-icon>
            <span>主页</span>
          </el-menu-item>
          <el-menu-item index="2">
            <el-icon>
              <Setting />
            </el-icon>
            <span>个人中心</span>
          </el-menu-item>
          <el-menu-item index="3" @click='avatar()'>
            <el-icon>
              <Setting />
            </el-icon>
            <span>头像设置</span>
          </el-menu-item>



        </el-menu>
      </el-aside>
      <el-container>

        <el-main>
          <el-tabs type="border-card">
            <el-tab-pane label="资料修改">
              <el-card>
                <h1>修改资料</h1>

                <el-form @submit.prevent="submitForm">
                  <el-form-item label="用户名">
                    <el-input v-model="name"></el-input>
                  </el-form-item>
                  <el-form-item label="密码">
                    <el-input v-model="password" type="password"></el-input>
                  </el-form-item>
                  <el-form-item label="性别">
                    <el-radio-group v-model="sex">
                      <el-radio label="男" />
                      <el-radio label="女" />
                      <el-radio label="保密" />
                    </el-radio-group>
                  </el-form-item>
                  <el-form-item>
                    <el-button type="primary" @click="submitForm">提交</el-button>
                  </el-form-item>
                </el-form>
              </el-card>

            </el-tab-pane>
            <el-tab-pane label="我的资料">
              <span>{{ msg }}</span>


            </el-tab-pane>
          </el-tabs>
        </el-main>
      </el-container>
    </el-container>
  </div>
  
</template>


<script lang="ts">

import { defineComponent } from 'vue';
import {
  ElForm,
  ElInput,
  ElButton,
  ElMessage
} from 'element-plus';
import axios from 'axios';
import { Setting, HomeFilled } from "@element-plus/icons-vue"
import { ref } from 'vue';
const msg = ref()
axios.get("http://127.0.0.1:5000/test3").then((res) => {
  console.log(res.data);
  msg.value = res.data;
});


export default defineComponent({
  name: 'App',
  components: { ElForm, ElInput, ElButton },
  data() {
    return {
      name: '',
      password: '',
      sex: ''
    };
  },
  methods: {
    async submitForm() {
      try {
        const response = await axios.post('http://127.0.0.1:5000/mytest', { name: this.name, password: this.password, sex: this.sex });
        console.log(response.data);
        ElMessage({
          showClose: true,
          message: '修改成功',
          type: 'success',
        })

      } catch (error) {

        ElMessage({
          showClose: true,
          message: "错误（详细错误请见控制台）",
          type: 'error',
        })

      }
    }
  },


});



</script>
<script lang="ts" setup>

const go_home = () => {
  ElMessage({ message: "正在返回主页..." })
  window.location.href = '/'

}

const avatar = () => {
  ElMessage({ message: "正在到设置头像" })
  window.location.href = '/setup/avatar'

}


</script>



